import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class ClientSenderService {

  path: string;
  port: number;

  constructor(private http: Http) { }

  init(path: string, port: number) {
    this.path = path;
    this.port = port;
  }

  private constructRoute(): string {
    return "http://" + this.path + ":" + this.port;
  }

  getAllMessages() {
    return this.http.get("/api/message/")
      .map(res => res.json());
  }

  sendMessage(message: string) {
    let body = new URLSearchParams();
    body.set('message', message);

    return this.http.post("/api/message/add/", body).map(res => res.json());
  }

  resetAllMessage() {
    return this.http.get("/api/message/reset").map(res => res.json());
  }
}



