import { Component, OnInit } from '@angular/core';
import { CountDownService } from '../count-down.service'
import { Observable } from 'rxjs/Rx';


@Component({
  selector: 'app-count-down',
  templateUrl: './count-down.component.html',
  styleUrls: ['./count-down.component.css']
})
export class CountDownComponent implements OnInit {

  endTime: any;
  time: any;
  timerInterval: any;

  constructor(private countDownService: CountDownService) { }

  ngOnInit() {
    this.countDownService.Init("127.0.0.1", 8080);
    this.GetTime();
    this.timerInterval = setInterval(() => this.CalculRemainingtime(), 1000);
  }

  ngOnDestroy() {
    clearInterval(this.timerInterval);
  }

  GetTime() {
    this.countDownService.GetTime().subscribe(endTime => this.endTime = endTime);
  }

  SetTime(time: number) {
    this.countDownService.SetTime(time)
      .subscribe(() => this.countDownService.GetTime()
        .subscribe(endTime => this.endTime = endTime)
      );
  }

  CalculRemainingtime() {
    var t = this.endTime - Date.now()
    this.time = {}
    this.time.now = Date.now()
    this.time.seconds = Math.floor((t / 1000) % 60);
    this.time.minutes = Math.floor((t / 1000 / 60) % 60);
    this.time.hours = Math.floor((t / (1000 * 60 * 60)) % 24);
    this.time.days = Math.floor(t / (1000 * 60 * 60 * 24));
  }
}



