import { Component, OnInit } from '@angular/core';
import {ClientSenderService} from '../client-sender.service'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  messages : any = [];

  constructor(private clientSender: ClientSenderService) 
  { 
  }

  ngOnInit() {
    this.clientSender.init("127.0.0.1", 8080);
    this.getAllMessage();
  }

  getAllMessage() 
  {
    this.clientSender.getAllMessages().subscribe(message => this.messages = message);
    this.messages.reverse();
  }

  sendMessage(message:string)
  {
    this.clientSender.sendMessage(message).subscribe(() => this.getAllMessage());
  }

  sendReset()
  {
    this.clientSender.resetAllMessage().subscribe(() => this.getAllMessage());
  }

}



